import React from 'react';
import colorTokens from './merged-colors.json';

const tokens = colorTokens.color.t1;

const DesignTokensDisplay = () => {

  const copyToClipboard = (value) => {
    // Create a temporary textarea element to copy the value to the clipboard
    const tempTextArea = document.createElement('textarea');
    tempTextArea.value = value;

    // Append the textarea to the DOM
    document.body.appendChild(tempTextArea);

    // Select the textarea and copy the value to the clipboard
    tempTextArea.select();
    document.execCommand('copy');

    // Remove the textarea from the DOM
    document.body.removeChild(tempTextArea);

    // Optionally, provide visual feedback to the user
    alert(`Copied: ${value}`);
  };

  return (
    <div>
      <h2>Color Tokens</h2>
      <table style={{ width: '100%', borderCollapse: 'collapse', marginTop: '10px' }}>
        <thead>
          <tr>
            <th>Token Name</th>
            <th>Light Value</th>
            <th>Dark Value</th>
          </tr>
        </thead>
        <tbody>
          {Object.entries(tokens).map(([tokenName, tokenData]) => (
            <tr key={tokenName}>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}>{tokenName}</td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}><p onClick={() => copyToClipboard(tokenData.value)}>{tokenData.value}</p> <div className="preview" style={{ backgroundColor: tokenData.value }}></div></td>
              <td style={{ border: '1px solid #ddd', padding: '8px' }}><p onClick={() => copyToClipboard(tokenData.darkValue)}> {tokenData.darkValue}</p> <div className="preview" style={{ backgroundColor: tokenData.darkValue }}></div></td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};


export default DesignTokensDisplay;